import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { getAllPosts } from '../../redux/actionCreators';
import store from '../../redux/store';
import Banner from '../Organisms/Banner';
import Publication from '../Organisms/Publication';

const Home = ({ posts }) => {

  useEffect(() => {
    store.dispatch(getAllPosts())
  }, [])

  return (
    <>
      <Banner
        color="dark-color"
        image=""
        title="Bienvenido a la experiencia más increible en educación online. Comienza hoy mismo a aprender"
        subtitle="Nuestro equipo ha desarrollado esta plataforma pensando en ti. Sabemos que estas buscando contenido de calidad. Aquí lo encontrarás"
        home
        poster="https://edteam-media.s3.amazonaws.com/specialities/big/10322ac1-7b43-4e12-9000-0c28e04178d6.png"
      />
      <main className="ed-grid m-grid-3">
        <div className="l-section m-cols-2">
          <h2>Ultimas publicaciones</h2>
          <div>
            {
              posts ?
                <div>
                  {
                    posts.map(p =>
                      <Publication
                        key={p.id}
                        title={p.title}
                        author={p.author}
                        date={p.date}
                        content={p.content}
                      />
                    )
                  }
                </div>
                :
                <p>No existen publicaciones</p>
            }
          </div>
        </div>
        <div>
          <h3>Lista de categorías</h3>
          <ul className="feature-list">
            <li><span>React.js</span></li>
            <li><span>React native</span></li>
            <li><span>Angular</span></li>
            <li><span>Vue.js</span></li>
            <li><span>HTML</span></li>
            <li><span>CSS</span></li>
          </ul>
        </div>
      </main>
    </>
  )
}

const mapStateToProps = state => ({
  posts: state.postReducer.posts
});

export default connect(mapStateToProps, {})(Home);


