import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { getAllCourses } from '../../redux/actionCreators';
import store from '../../redux/store';
import Banner from '../Organisms/Banner';
import Card from '../Organisms/Card';

const Courses = ({ courses }) => {

  useEffect(() => {
    store.dispatch(getAllCourses())
  }, [])

  return (
    <>
      <Banner
        color="dark-color"
        image={{
          src: "https://as01.epimg.net/epik/imagenes/2020/01/17/portada/1579264345_014526_1579264425_noticia_normal.jpg",
          alt: "Banner Especialidades"
        }}
        title="Nuestros cursos"
        subtitle="Comienza desde cero hoy mismo en tu camino a dominar la tecnología"
      />
      {
        courses &&
        <main className="ed-grid m-grid-5">
          {
            courses.map(c => (
              <Card
                cardId={c.id}
                path="cursos"
                picture={c.picture}
                name={c.name}
              />
            ))
          }
        </main>
      }
    </>
  )
}

const mapStateToProps = state => ({
  courses: state.courseReducer.courses
});

export default connect(mapStateToProps, {})(Courses);
