import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { getAllSpecialities } from '../../redux/actionCreators'
import store from '../../redux/store'
import Banner from '../Organisms/Banner'
import Card from '../Organisms/Card'

const Specialities = ({ specialities }) => {

  useEffect(() => {
    store.dispatch(getAllSpecialities())
  }, [])

  return (
    <>
      <Banner
        color="first-color"
        image={{
          src: "https://as01.epimg.net/epik/imagenes/2020/01/17/portada/1579264345_014526_1579264425_noticia_normal.jpg",
          alt: "Banner Especialidades"
        }}
        title="Especialidades"
        subtitle="Domina una tecnología con las rutas de aprendizaje que te ofrecemos"
      />
      {
        specialities &&
        <main className="ed-grid m-grid-3">
          {
            specialities.map(s => (
              <Card
                key={s.id}
                cardId={s.id}
                path="especialidades"
                picture={s.picture}
                name={s.name}
              />
            ))
          }
        </main>
      }
    </>
  )
}

const mapStateToProps = state => ({
  specialities: state.specialityReducer.specialities
});

export default connect(mapStateToProps, {})(Specialities);

